.intro {
  margin-top: -29px;

  @include vp-1199 {
    margin-top: 0;
  }
}

.intro .container {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: 1fr 122px;
  padding-right: 287px;

  @include vp-1199 {
    padding-right: 45px;
    grid-template-rows: auto auto;
  }

  @include vp-767 {
    padding-right: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.intro__list {
  margin: 0;
  list-style: none;
  padding: 0 0 66px 3px;
  text-align: end;
  align-self: end;


  @include vp-1199 {
    padding: 0 0 57px 0;
    margin-right: 24px;
  }

  @include vp-767 {
    padding: 0 0 54px 0;
    margin-right: -15px;
  }
}

.intro__item {
  @include font-settings ($ff-roboto, 400, 14px, 1.5, $color-default-white);
}

.intro p {
  @include font-settings ($ff-alumni, 600, 48px, 1.2, $color-default-white);
  margin: 0;
  letter-spacing: 0.03em;
  text-align: end;
  padding-top: 111px;
  padding-right: 115px;
  margin-bottom: 14px;

  @include vp-1199 {
    font-size: 34px;
    padding-top: 50px;
    padding-right: 33px;
    margin-bottom: 5px;
  }

  @include vp-767 {
    font-size: 28px;
    line-height: 1.21;
    padding-right: 4px;
    margin-bottom: 12px;
  }
}

.intro h1 {
  @include font-settings ($ff-alumni, 700, 93px, 1.2, $color-default-white);
  margin: 0;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: end;
  padding-right: 64px;
  margin-bottom: 24px;

  @include vp-1199 {
    text-transform: none;
    font-size: 74px;
    line-height: 1.18;
    letter-spacing: 0.03em;
    margin-bottom: 36px;
    padding-right: 30px;
  }

  @include vp-767 {
    padding-right: 0;
    margin-left: auto;
    margin-bottom: 21px;
  }
}

.intro__btn {
  width: 236px;
  min-height: 38px;
  margin: 18px 0 43px 31px;
  padding: 5px;
  text-align: center;
  grid-column: 2 / 3;

  @include vp-1199 {
    width: 187px;
    min-height: 61px;
    margin: 0 0 23px 2px;
    padding-top: 4px;
  }

  @include vp-767 {
    min-height: 63px;
    min-width: 100%;
    margin: 0;
  }
}

.intro__img-wrapper {
  max-width: 500px;
  max-height: 556px;
  grid-column: 3 / 4;
  grid-row: 1 / 3;

  @include vp-1199 {
    max-width: 324px;
    max-height: 390px;
  }

  @include vp-767 {
    display: none;
  }
}

.intro__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
