  color: #ffffff;
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 16px;
    line-height: 1.18;
  }
}

.footer__socials {
  margin-left: auto;
}
@media (max-width: 767px) {
  .footer__socials {
    margin: 0;
    padding: 0;
  }
}

.socials__list {
  min-width: 150px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
@media (max-width: 1199px) {
  .socials__list {
    min-width: 100px;
    margin: 0 7px 0 0;
  }
}
@media (max-width: 767px) {
  .socials__list {
    min-width: 100%;
    justify-content: flex-start;
  }
}

.socials__item {
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border-radius: 50%;
  margin-bottom: 15px;
}

.socials__item:not(:last-child) {
  margin-right: 11px;
}

